html {
  perspective: 1000px;
}
#case_c {
  animation: letf 0.5s;
}
#case_c .row_col {
  transition: transform 0.3s, filter 0.3s;
  padding-right: 0;
  margin-bottom: 1rem;
}
#case_c .row_col .item {
  height: 100%;
  background-color: white;
  border-radius: 15px;
  overflow: hidden;
}
#case_c .row_col .item .item_img {
  position: relative;
  height: 45%;
  overflow: hidden;
}
#case_c .row_col .item .item_img .antIcon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
}
#case_c .row_col .item .item_img img {
  width: 100%;
}
#case_c .row_col .item .item_content {
  padding: 0.7rem;
}
#case_c .row_col .item .item_content .item_title {
  font-size: inherit;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.85);
}
#case_c .row_col .item .item_content .item_introduce {
  text-decoration: none;
}
#case_c .row_col .item .item_content .item_introduce a {
  display: block;
  color: rgba(0, 0, 0, 0.5);
  animation: color 0.3s;
}
#case_c .row_col .item .item_content .item_introduce a:hover {
  color: rgba(0, 0, 0, 0.7);
}
#case_c .row_col.zFloat {
  z-index: 9999;
  transform: scale(1.05);
}
.row_col.sink {
  filter: blur(5px);
  transform: scale(0.95);
}
@media (max-width: 991px) {
  .item_content {
    height: 4rem;
  }
  .item_content .item_introduce {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
@keyframes letf {
  0% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0px);
  }
}
