.circle {
    border-radius: 50%;
}

.center {
    position : absolute;
    top      : 50%;
    left     : 50%;
    transform: translate(-50%, -50%);
}

.inner {
    position        : relative;
    width           : 30px;
    height          : 30px;
    background-color: #384c50;
    box-shadow      : 0 0 5px -1px rgb(18 20 20), 0 0 1px 0px rgb(18 20 20) inset;
}

.inner .box1 {
    width           : 24px;
    height          : 24px;
    background-color: rgb(22, 26, 27);
    box-shadow      : 0 0 1px 0px #52fefe
}

.inner .box2 {
    width           : 22px;
    height          : 22px;
    background-color: #fff;
    box-shadow      : 0 0 3px -2px #52fefe, 0 0 1px 1px #52fefe inset;
}

.inner .box3 {
    width           : 18px;
    height          : 18px;
    background-color: #073c4b;
    box-shadow      : 0 0 3px 0px #52fefe, 0 0 1px 0px #52fefe inset;
}


.inner .box4 {
    width           : 11px;
    height          : 11px;
    border          : 1px solid #52fefe;
    background-color: #fff;
    box-shadow      : 0 0 3px 0px #52fefe, 0 0 2px 0px #52fefe inset;
}

.inner .box5 {
    width     : 6px;
    height    : 6px;
    border    : .5px solid #1b4e5f;
    box-shadow: 0 0 3px 0px #52fefe, 0 0 2px 1px #52fefe inset;
}

.inner .box6 {
    position : relative;
    width    : 100%;
    height   : 100%;
    animation: rotate 3s linear infinite;

}

.inner .box6 .coil {
    position        : absolute;
    border-radius   : 15%;
    width           : 3px;
    height          : 2px;
    top             : calc(50% - 11px);
    left            : calc(52% - 2px);
    background-color: #073c4b;
    box-shadow      : 0 0 1px #52fefe inset;
    transform       : rotate(calc(var(--i) * 45deg));
    transform-origin: center 11.1px;
}

@keyframes rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0);
    }

    100% {
        transform: translate(-50%, -50%) rotate(360deg);
    }
}