header {
  background-color: #ffffff;
  font-size: 20px;
  transform: filter 0.3s;
}
header .container {
  padding: 0;
}
header .container .navbar .navbar-brand {
  font-size: 1.5rem;
  color: #7f7f7f;
}
header .container .navbar .navbar-brand img {
  margin-right: 0.5rem;
}
header .container .navbar #navbarNav {
  flex-direction: row-reverse;
}
header .container .navbar .navbar-nav .nav-item {
  margin-right: 2rem;
}
header .container .navbar .navbar-nav .nav-item .nav-link {
  color: #7f7f7f;
}
header .container .navbar .navbar-nav .nav-item .nav-link span {
  font-size: 2rem;
}
header .container .navbar .navbar-nav .nav-item .nav-link:hover {
  animation: nana 0.5s;
}
header .container .navbar .navbar-nav .nav-item:nth-of-type(4) {
  margin-right: 0;
}
@keyframes nana {
  0% {
    transform: scale(1);
    color: #7f7f7f;
  }
  50% {
    transform: scale(1.2);
    color: #47966a;
  }
  100% {
    transform: scale(1);
    color: #7f7f7f;
  }
}
